.burger {
  margin-right: 20px;
  position: relative;
  display: none;
  width: 50px;
  height: 34px;

  cursor: pointer;
  background-color: transparent;

  @media (max-width: $media-m) {
    display: block;
  }

  @media (max-width: $media-m) {
    width: 35px;
    height: 20px;
  }

  &::before,
  &::after {
    content: "";
  }

  span,
  &::before,
  &::after {
    display: block;
    width: 50px;
    height: 5px;

    position: absolute;

    background-color: #ffffff;
    border-radius: 5px;

    @media (max-width: $media-m) {
      width: 35px;
      height: 3px;
    }
  }

  span {
    top: 50%;
    transform: translateY(-50%);
  }

  &::before {top: 0}
  &::after {bottom: 0}
}











