.feedback {
  position: relative;
  z-index: 5;
  background-color: #ffffff;
}

.feedback__bg {
  position: absolute;
  top: -150px;
  left: 0;
  bottom: -200px;
  right: 0;
  z-index: -1;

  @media (max-width: $media-m) {
    top: -100px;
    bottom: -100px;
  }

  @media (max-width: $media-s) {
    top: -50px;
    bottom: -50px;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.feedback__inner {
  padding: 100px 0;

  @media (max-width: $media-m) {
    padding: 70px 0;
  }
}

.feedback__title {
  margin-bottom: 50px;

  text-align: center;
  color: #1A1A1A;
  font-size: 60px;
  font-weight: 600;

  @media (max-width: $media-m) {
    font-size: 50px;
    margin-bottom: 35px;
  }

  @media (max-width: $media-s) {
    font-size: 42px;
  }

  @media (max-width: $media-sm) {
    font-size: 30px;
  }
}

.feedback__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedback__input {
  display: block;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 15px;
  padding: 23px 0;

  font-size: 30px;
  text-align: center;
  color: #101111;

  background-color: #EAEAEA;

  @media (max-width: $media-m) {
    font-size: 25px;
  }

  @media (max-width: $media-s) {
    font-size: 20px;
  }

  @media (max-width: $media-sm) {
    padding: 15px 0;
  }

  &:nth-child(1) {
    border-radius: 40px 40px 0 0;

    @media (max-width: $media-sm) {
      border-radius: 20px 20px 0 0;
    }
  }

  &:nth-child(2) {
    margin-bottom: 35px;
    border-radius: 0 0 40px 40px;

    @media (max-width: $media-s) {
      margin-bottom: 20px;
    }

    @media (max-width: $media-sm) {
      border-radius: 0 0 20px 20px;
    }
  }
}

.feedback__btn {
  font-size: 32px;
  padding: .7em 3.6em;
  color: #101111;

  @media (max-width: $media-m) {
    font-size: 28px;
  }

  @media (max-width: $media-s) {
    font-size: 24px;
  }

  @media (max-width: $media-sm) {
    font-size: 20px;
    padding: .7em 2em;
  }
}