.services {
  position: relative;
  padding: 350px 0 100px;

  @media (max-width: $media-m) {
    padding: 200px  0 70px;
  }

  @media (max-width: $media-s) {
    padding: 100px  0 50px;
  }

  &:after {
    content: url("../images/services-decor.svg");
    display: block;
    position: absolute;
    left: 0;
    top: 7%;
    opacity: .1;

    @media (max-width: $media-m) {
      display: none;
    }
  }
}

.services__title {
  margin-bottom: 120px;

  @media (max-width: $media-m) {
    margin-bottom: 100px;
  }

  @media (max-width: $media-s) {
    margin-bottom: 50px;
  }
}

.services__wrap {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 25px;
}

.services__col {
  margin-right: 155px;

  @media (max-width: $media-m) {
    width: 415px;
    margin-right: 0;
  }

  @media (max-width: 900px) {
    width: 360px;
  }

  @media (max-width: $media-s) {
    width: 100%;
    margin-bottom: 50px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:nth-child(2) {
    .services__icon:after {
      @media (max-width: $media-m) {
        content: url("../images/services-decor-line.svg");

        width: auto;
        height: auto;
        left: -95px;
        top: 120px;
        transform: none;
      }

      @media (max-width: 900px) {
        left: -70px;
        top: 112px;
      }
    }
  }

  &:nth-child(3) {
    margin-right: 0;

    .services__icon:after {
      display: none;
    }
  }
}

.services__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.services__info {
  max-width: 270px;
}

.services__item-title {
  margin-bottom: 15px;
  font-size: 26px;
  font-weight: 600;
}

.services__item-text {
  font-size: 19px;
}

.services__icon {
  position: relative;
  z-index: 1;
  width: 250px;
  height: 250px;
  margin-bottom: 35px;

  display: inline-flex;
  align-items: center;
  justify-content: center;

  border-radius: 50%;
  border: 4px solid #ffffff;
  box-shadow: 0 0 50px rgba(#fff, .4), 0 0 50px rgba(#fff, .4) inset;
  background-color: #222222;

  @media (max-width: $media-s) {
    margin-bottom: 15px;
  }

  &:after {
    content: url('../images/services/line.svg');
    display: block;
    width: 170px;
    height: 30px;
    position: absolute;
    z-index: 1;
    left: 100%;
    top: 50%;

    transform: translateY(-50%);

    @media (max-width: $media-s) {
      display: none;
    }
  }
}

.services__btn {
  text-align: center;
}