.guarantees {
  padding: 50px 0 100px;

  @media (max-width: $media-m) {
    padding: 50px 0;
  }
}

.guarantees__title {
  margin-bottom: 140px;

  @media (max-width: $media-m) {
    margin-bottom: 50px;
  }

  @media (max-width: $media-sm) {
    margin-bottom: 30px;
  }
}

.guarantees__wrap {
  display: flex;
  margin: 0 -70px 132px;

  @media (max-width: $media-l) {
    margin: 0 -20px 80px;
  }

  @media (max-width: $media-m) {
    flex-wrap: wrap;
  }

  @media (max-width: $media-m) {
    margin: -20px -10px 70px;
  }

  @media (max-width: $media-sm) {
    margin: 0 0 30px 0;
  }
}

.guarantees__col {
  width: 25%;
  padding: 0 70px;

  @media (max-width: $media-l) {
    padding: 0 20px;
  }

  @media (max-width: $media-m) {
    width: 50%;
    padding: 20px 10px;
  }

  @media (max-width: $media-sm) {
    width: 100%;
    padding: 0;
    margin-bottom: 35px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.guarantees__item {
  text-align: center;
}

.guarantees__icon {
  margin-bottom: 50px;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  width: 206px;
  height: 206px;

  @media (max-width: $media-s) {
    margin-bottom: 20px;
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0px 0px 10px rgba(#fff, 1));
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='white' stroke-width='3' stroke-dasharray='95' stroke-dashoffset='66' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 30px;
  }
}

.guarantees__text {

}

.guarantees__btn-wrap {
  text-align: center;
}