.works {
  position: relative;
  padding: 100px 0 350px;

  @media (max-width: $media-m) {
    padding: 70px 0 250px;
  }

  @media (max-width: $media-s) {
    padding: 50px 0 200px;
  }
}

.works__wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: -37px -18px;

  @media (max-width: $media-s) {
    justify-content: center;
  }

  &:before {
    content: url("../images/works/decor-top.svg");
    display: block;
    position: absolute;
    top: -2px;
    left: -22px;
    z-index: 1;
    filter: drop-shadow(0 4px 30px rgba($color-text-secondary, 1)) drop-shadow(0 4px 50px rgba($color-text-secondary, .7));
  }
  &:after {
    content: url("../images/works/decor-bottom.svg");
    display: block;
    position: absolute;
    bottom: 36px;
    right: -22px;
    z-index: 1;
    filter: drop-shadow(0 4px 30px rgba($color-text-secondary, 1)) drop-shadow(0 4px 50px rgba($color-text-secondary, .7));
  }
}

.works__col {
  width: 33.333%;
  padding: 37px 18px;

  @media (max-width: $media-m) {
    width: 50%;
  }

  @media (max-width: $media-sm) {
    width: 80%;
  }

  @media (max-width: $media-sm) {
    width: 100%;
  }
}

.works__img {
  margin-bottom: 10px;
  border-radius: 22px;
  overflow: hidden;

  img {
    width: 100%;
    max-width: 100%;
    display: block;
    object-fit: cover;
  }
}

.works__name {
  font-size: 40px;
  font-weight: 400;

  @media (max-width: $media-s) {
    font-size: 30px;
  }
}