/* STYLE ZIRO */
*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
  background: none;
}

a {
  text-decoration: none;
  color: inherit;
}

input, textarea, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

ul, li {
  list-style: none;
}

img {
  vertical-align: top;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.eot");
  src: local("Gilroy Regular"), local("Gilroy-Regular"), url("../fonts/Gilroy-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff"), url("../fonts/Gilroy-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Medium.eot");
  src: local("Gilroy Medium"), local("Gilroy-Medium"), url("../fonts/Gilroy-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff"), url("../fonts/Gilroy-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Semibold.eot");
  src: local("Gilroy Semibold"), local("Gilroy-Semibold"), url("../fonts/Gilroy-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Semibold.woff2") format("woff2"), url("../fonts/Gilroy-Semibold.woff") format("woff"), url("../fonts/Gilroy-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Bold.eot");
  src: local("Gilroy Bold"), local("Gilroy-Bold"), url("../fonts/Gilroy-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff"), url("../fonts/Gilroy-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
html,
body {
  height: 100%;
  font-family: Gilroy, sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
  color: #fff;
  background-color: #222;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 20px;
  box-sizing: content-box;
}

.page {
  flex: 1 1 auto;
}

.section-title {
  font-size: 54px;
  font-weight: 700;
}
@media (max-width: 1080px) {
  .section-title {
    font-size: 50px;
  }
}
@media (max-width: 575px) {
  .section-title {
    font-size: 35px;
  }
}

.text-dedicated {
  color: #00FFE0;
}

.burger {
  margin-right: 20px;
  position: relative;
  display: none;
  width: 50px;
  height: 34px;
  cursor: pointer;
  background-color: transparent;
}
@media (max-width: 1080px) {
  .burger {
    display: block;
  }
}
@media (max-width: 1080px) {
  .burger {
    width: 35px;
    height: 20px;
  }
}
.burger::before, .burger::after {
  content: "";
}
.burger span, .burger::before, .burger::after {
  display: block;
  width: 50px;
  height: 5px;
  position: absolute;
  background-color: #ffffff;
  border-radius: 5px;
}
@media (max-width: 1080px) {
  .burger span, .burger::before, .burger::after {
    width: 35px;
    height: 3px;
  }
}
.burger span {
  top: 50%;
  transform: translateY(-50%);
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}

.btn {
  display: inline-block;
  padding: 0.9em 2em;
  font-family: Gilroy, sans-serif;
  color: #fff;
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  cursor: pointer;
  background-color: #00FFE0;
  box-shadow: 0 2.8951048851px 4.3426570892px 0 rgba(255, 255, 255, 0.4) inset, 0 4px 80px 0 rgba(0, 0, 0, 0.25);
  border-radius: 0 1.5em 0 1.5em;
  border: 1px solid transparent;
  transition: background-color linear 0.2s, boder-color linear 0.2s;
}
.btn:hover {
  background-color: transparent;
  border-color: #00FFE0;
}

.btn--light {
  background-color: rgba(0, 255, 224, 0.5);
  box-shadow: 0 2px 3px 0 rgba(255, 255, 255, 0.4) inset;
}
.btn--light:hover {
  border-color: rgba(0, 255, 224, 0.5);
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(25px);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.header__wrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1080px) {
  .header__wrap {
    justify-content: flex-start;
  }
}

.header__left {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 150px;
}
@media (max-width: 1080px) {
  .header__logo {
    margin-right: 45px;
  }
}
@media (max-width: 575px) {
  .header__logo {
    margin-right: 20px;
  }
}

.social {
  display: flex;
  align-items: center;
}
@media (max-width: 1080px) {
  .social {
    width: 300px;
    flex-direction: column;
    text-align: right;
    position: fixed;
    top: 100%;
    right: 0;
    z-index: 1;
    background-color: #fff;
  }
}

.social__link {
  display: block;
  margin-right: 100px;
  font-size: 18px;
  font-weight: 700;
}
.social__link:hover {
  filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.5));
}
@media (max-width: 1080px) {
  .social__link {
    display: none;
    width: 100%;
    padding: 15px 30px 15px 0;
    margin-right: 0;
    font-size: 30px;
    color: #222222;
    background-color: #fff;
  }
}

.header__phone {
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;
  font-size: 21px;
  font-weight: 700;
}
@media (max-width: 575px) {
  .header__phone {
    font-size: 16px;
  }
}
.header__phone:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 20px;
  height: 20px;
  transform: translateY(-50%);
  background-image: url("../images/tel-icon.svg");
}

.header__btn {
  padding: 0.1em 1.7em;
  font-size: 20px;
  border-radius: 0 0.8em 0 0.8em;
}
@media (max-width: 1080px) {
  .header__btn {
    padding: 1em 3em;
    border-radius: 0 1.5em 0 1.5em;
  }
}
@media (max-width: 768px) {
  .header__btn {
    padding: 0.7em 2.5em;
  }
}
@media (max-width: 575px) {
  .header__btn {
    padding: 0.4em 1.5em;
    font-size: 16px;
    border-radius: 0 1em 0 1em;
  }
}

