.order {
  position: relative;
  padding: 100px 0;

  @media (max-width: $media-m) {
    padding: 70px 0;
  }

  @media (max-width: $media-s) {
    padding: 50px 0;
  }

  &:after {
    content: url("../images/order-decor.svg");
    display: block;
    position: absolute;
    top: -100px;
    right: 0;

    filter:
            drop-shadow(0 0 1px rgba($color-text-secondary, 1))
            drop-shadow(0 0 10px rgba($color-text-secondary, 1))
            drop-shadow(0 0 20px rgba($color-text-secondary, 1))
            drop-shadow(0 0 50px rgba($color-text-secondary, 1))
            drop-shadow(0 0 70px rgba($color-text-secondary, 1))
            drop-shadow(0 0 150px rgba($color-text-secondary, 1));

    @media (max-width: $media-s) {
      opacity: .5;
    }
  }

  &:before {
    content: url("../images/order-decor-two.svg");
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    opacity: .1;
  }
}

.order__title {
  margin-bottom: 93px;

  @media (max-width: $media-m) {
    margin-bottom: 50px;
  }

  @media (max-width: $media-s) {
    margin-bottom: 30px;
  }
}

.order__item {
  margin-bottom: 87px;
  padding-left: 108px;
  position: relative;

  @media (max-width: $media-s) {
    padding-left: 0;
    margin-bottom: 50px;
  }

  &:first-child {
    & .order__decor {
      border-top: 3px solid #fff;
      border-top-left-radius: 13px;

      &:after {
        top: -7px;
      }
    }
  }

  &:last-child {
    margin-bottom: 0;

    & .order__decor {
      border-left: none;
      border-bottom: none;
    }
  }
}

.order__decor {
  position: absolute;
  left: 0;
  top: 32px;
  width: 74px;
  height: calc(100% + 98px);

  border-left: 3px solid #fff;
  border-bottom: 3px solid #fff;
  border-radius: 0 0 0 13px;

  @media (max-width: $media-m) {
    width: 60px;

    top: 23px;
    height: calc(100% + 96px);
  }

  @media (max-width: $media-s) {
    display: none;
  }

  &:after {
    content: "";
    width: 11px;
    height: 11px;
    display: block;

    position: absolute;
    right: 0;
    top: 4px;
    z-index: 1;

    background-color: #fff;
    transform: rotate(-45deg);

    @media (max-width: $media-m) {
      top: 2px;
    }
  }
}

.order__item-title {
  margin-bottom: 12px;

  font-size: 60px;
  color: $color-text-secondary;
  font-weight: 600;

  @media (max-width: $media-m) {
    font-size: 45px;
  }

  @media (max-width: $media-sm) {
    font-size: 24px;
  }
}

.order__text {
  font-size: 20px;
  font-weight: 500;
}