.advantages {
  position: relative;
  padding: 100px 0;

  @media (max-width: $media-m) {
    padding: 70px 0;
  }

  @media (max-width: $media-s) {
    padding: 50px 0;
  }

  &:before {
    content: url("../images/advantages-decor.svg");
    display: block;
    position: absolute;
    right: 0;
    top: 20%;
    opacity: .1;
  }
}

.advantages__title {
  margin-bottom: 84px;

  @media (max-width: $media-m) {
    margin-bottom: 50px;
  }

  @media (max-width: $media-s) {
    margin-bottom: 20px;
  }
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  margin: -50px -40px;

  @media (max-width: $media-m) {
    margin: 0;
  }
}

.advantages__col {
  width: 50%;
  padding: 50px 40px;

  @media (max-width: $media-m) {
    width: 100%;
    padding: 0;
    margin: 0 0 30px 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &--center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.advantages__item-title {
  position: relative;
  margin-bottom: 40px;
  padding-left: 73px;

  font-size: 60px;
  font-weight: 600;
  color: $color-text-secondary;

  @media (max-width: $media-l) {
    font-size: 45px;
  }

  @media (max-width: $media-sm) {
    padding-left:0;
    margin-bottom: 20px;
    font-size: 24px;
  }
}

.advantages__value {
  position: absolute;
  left: 0;
  top: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 100px;
  color: #ffffff;

  transform: translateY(-50%);

  @media (max-width: $media-sm) {
    font-size: 70px;
    position: relative;
    top: auto;
    left: auto;
    transform: none;
    justify-content: normal;
  }

  &:before {
    content: "";
    position: absolute;
    left: -4px;
    z-index: -1;
    width: 53px;
    height: 53px;

    border: 3px solid $color-text-secondary;
    transform: rotate(-15deg);

    @media (max-width: $media-sm) {
      margin-bottom: 20px;
      top: 20px;
      left: -10px;
      z-index: -1;
      width: 48px;
      height: 48px;
    }
  }
}

.advantages__text {
  padding-left: 100px;
  font-size: 20px;
  line-height: 1.45;
  font-weight: 500;

  @media (max-width: $media-sm) {
    padding-left: 0;
  }
}

.advantages__btn {
  font-size: 32px;

  @media (max-width: $media-s) {
    font-size: 24px;
  }

  @media (max-width: $media-sm) {
    font-size: 20px;
  }
}