.footer {
  position: relative;
  padding: 300px 0 100px;

  @media (max-width: $media-m) {
    padding: 200px 0 50px;
  }

  @media (max-width: $media-s) {
    padding-top: 100px;
  }

  &:after {
    content: url("../images/footer-decor.svg");
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;

    filter:
            drop-shadow(0 0 1px rgba($color-text-secondary, 1))
            drop-shadow(0 0 10px rgba($color-text-secondary, 1))
            drop-shadow(0 0 20px rgba($color-text-secondary, 1))
            drop-shadow(0 0 50px rgba($color-text-secondary, 1))
            drop-shadow(0 0 70px rgba($color-text-secondary, 1))
            drop-shadow(0 0 150px rgba($color-text-secondary, 1));

    @media (max-width: $media-m) {
      opacity: .4;
    }
  }
}

.footer__inner {
  display: flex;
  justify-content: center;
}

.footer__wrap {
  display: flex;
  max-width: 800px;

  @media (max-width: $media-sm) {
    flex-wrap: wrap;
  }
}

.footer__list {
  margin-right: 130px;
  width: 50%;

  @media (max-width: $media-m) {
    margin-right: 60px;
  }

  @media (max-width: $media-sm) {
    width: 100%;
    margin-right: 0;
  }

  &:last-child {
    margin-right: 0;
  }
}

.footer__item {
  margin-bottom: 26px;

  &:last-child {
    margin-bottom: 0;
  }
}

.footer__title {
  font-size: 27px;
  font-weight: 600;
}

.footer__text {
  font-weight: 400;
}