.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;

  background-color: rgba(#fff, .1);
  backdrop-filter: blur(25px);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.header__wrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: $media-m) {
    justify-content: flex-start
  }
}

.header__left {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 150px;

  @media (max-width: $media-m) {
    margin-right: 45px;
  }

  @media (max-width: $media-sm) {
    margin-right: 20px;
  }
}

.social {
  display: flex;
  align-items: center;

  @media (max-width: $media-m) {
    width: 300px;
    flex-direction: column;
    text-align: right;

    position: fixed;
    top: 100%;
    right: 0;
    z-index: 1;

    background-color: #fff;
  }
}

.social__link {
  display: block;
  margin-right: 100px;
  font-size: 18px;
  font-weight: 700;

  &:hover {
    filter: drop-shadow(5px 5px 10px rgba(#000, .5));
  }

  @media (max-width: $media-m) {
    display: none;
    width: 100%;
    padding: 15px 30px 15px 0;
    margin-right: 0;

    font-size: 30px;
    color: #222222;

    background-color: #fff;
  }
}

.header__phone {
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;

  font-size: 21px;
  font-weight: 700;

  @media (max-width: $media-sm) {
    font-size: 16px;
  }

  &:before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    width: 20px;
    height: 20px;

    transform: translateY(-50%);
    background-image: url('../images/tel-icon.svg');
  }
}

.header__btn {
  padding: 0.1em 1.7em;

  font-size: 20px;
  border-radius: 0 .8em 0 .8em;

  @media (max-width: $media-m) {
    padding: 1em 3em;
    border-radius: 0 1.5em 0 1.5em;
  }

  @media (max-width: $media-s) {
    padding: .7em 2.5em;
  }

  @media (max-width: $media-sm) {
    padding: .4em 1.5em;
    font-size: 16px;
    border-radius: 0 1em 0 1em;
  }
}